import styled from 'styled-components';


const ContainerDefault = styled.div`
    width:100%;
    height:100%;
    position:absolute;
    left:0;
    top:0;
`;
const Container = styled(ContainerDefault)`
    display:flex;
    justify-content:center;
    align-items:center;
    z-index:9999;
`;

const Content = styled.div`
    position:relative;
    z-index:1;
    width:100%;
    width:680px;
`;
const CloseArea = styled(ContainerDefault)`
    z-index:0;
`;

export const Styled = {
    Container,
    Content,
    CloseArea,
};